@import url(https://fonts.googleapis.com/css?family=Roboto&display=swap);
.decoratorDialog .sectionTitle {
    height: 100%;
    display: flex;
    align-items: flex-end;
}

.decoratorDialog .dialogGrid_rightAlignedLabels .sectionTitle {
    justify-content: flex-end;
}

.decoratorDialog .sectionTitle span {
    font-weight: bold;
    color: #444;
    font-size: 18px;
    margin-bottom: 8px;
}
.decoratorDialog .wideSelectBox {
    display: inherit;
}

.decoratorDialog .preview {
    border-radius: 10px;
    padding: 20px;
    box-sizing: border-box;
    width: 100%;
    display: flex;
    flex-direction: column;
    background-color: whitesmoke;
    box-shadow: -3px 5px 10px -4px rgba(0,0,0,0.21);
    max-width: 625px;
}

.decoratorDialog .preview_list {
    min-width: 300px;
    position: -webkit-sticky;
    position: sticky;
    top: 0;
}

.decoratorDialog .listPreviewBox {
    height: 100%;
}

.decoratorDialog .preview-content {
    height: 220px;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
}

.decoratorDialog .preview-content_withPointer {
    cursor: pointer;
}

.decoratorDialog .preview-title {
    color: darkgrey;
    font-size: 12pt;
}

.decoratorDialog .demonstrationElement {
    width: 100%;
    height: 92%;
    margin-top: 10px;
    display: flex;
    align-items: center;
    justify-content: center;
    word-break: break-word;
    overflow: hidden;
    border-radius: 7px;
    border: 1px solid lightgray;
    padding-left: 20px;
    padding-right: 20px;
}

.decoratorDialog .demonstrationElement-text {
    display: flex;
    flex-flow: column;
    cursor: text;
    text-align: left;
    margin-left: 20px;
    overflow: visible;
    max-height: 100%;
}

.decoratorDialog .demonstrationElement-text div {
    visibility: visible;
    visibility: initial;
    background-color: inherit;
    margin-top: inherit;
    margin-bottom: inherit;
    border: inherit;
    -webkit-border-image: none;
            border-image: none;
    padding: inherit;
    width: 100%;
}

.decoratorDialog .demonstrationElement-text span {
    white-space: pre-wrap;
}

.decoratorDialog .listItemBeginning {
    display: inline-block;
}
.colorPicker {
    margin-left: 20px;
    margin-top: -50px;
    position: relative;
}

.colorPicker_bottomAligned {
    margin-top: -410px !important;
}

.colorPicker-popOver {
    position: fixed;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    z-index: 4;
}

.colorPicker-chromeBox {
    position: absolute;
    z-index: 5;
}
.decoratorDialog .listItemSpan {
    min-width: 105px;
}

.decoratorDialog .optionalSettings {
    -webkit-transition: all 0.3s ease-out;
    transition: all 0.3s ease-out;
}

.optionalSettings_main {
    -webkit-transition: all 0.4s ease-out !important;
    transition: all 0.4s ease-out !important;
}

.decoratorDialog .optionalSettings_hidden {
    max-height: 0;
    overflow: hidden;
    opacity: 0;
}

.decoratorDialog .optionalSettings_shown {
    max-height: none;
    opacity: 1;
}

.decoratorDialog .listTypeSettings {
    margin-top: 20px;
    padding-top: 20px;
    border-top: 1px solid lightgrey;
}
.decoratorDialog .labeledCheckbox_bold span {
    font-weight: bold;
}

.decoratorDialog .labeledCheckbox_italic span {
    font-style: italic;
}

.decoratorDialog .labeledCheckbox_underlined span {
    text-decoration: underline;
}

.decoratorDialog .labeledCheckbox_stroke span {
    text-decoration: line-through;
}

.decoratorDialog .labeledCheckbox_uppercase span {
    text-transform: uppercase;
}

.decoratorDialog .labeledCheckbox_lowercase span {
    text-transform: lowercase;
}

.decoratorDialog .labeledCheckbox_smallCaps span {
    -webkit-font-feature-settings: "c2sc";
            font-feature-settings: "c2sc";
    font-variant: small-caps;
}

.decoratorDialog .stylingsSpan {
    min-width: 115px;
}
.decoratorDialog .directionControlsContainer {
    width: 245px;
    height: 210px;
    margin-right: 15px;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: -5px;
}

.decoratorDialog .directionsControls {
    padding: 15px;
    display: inline-grid;
    grid-template-columns: repeat(3, auto);
    align-items: center;
}

.decoratorDialog .centralColumn {
    display: flex;
    flex-flow: column;
    align-items: center;
}

.decoratorDialog .crossGrid {
    display: grid;
    grid-template-rows: repeat(3, -webkit-min-content);
    grid-template-rows: repeat(3, min-content);
    grid-template-columns: repeat(3, -webkit-min-content);
    grid-template-columns: repeat(3, min-content);
    grid-template-areas: "r1 r1 r1"
                         "r2c1 r2c2 r2c3"
                         "r3 r3 r3";
    justify-items: center;
    align-items: center;
}

.decoratorDialog #r1 {
    grid-area: r1;
}

.decoratorDialog #r2c1 {
    grid-area: r2c1;
}

.decoratorDialog #r2c2 {
    grid-area: r2c2;
}

.decoratorDialog #r2c3 {
    grid-area: r2c3;
}

.decoratorDialog #r3 {
    grid-area: r3;
}

.decoratorDialog .checkboxesKit {
    margin: 10px;
}

.decoratorDialog .gridCell {
    width: 30px;
    height: 30px;
    display: flex;
    align-items: center;
    justify-content: center;
}
.decoratorDialog .header {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.decoratorDialog .header-title {
    font-weight: bold;
    font-size: 22px;
    height: 3rem !important;
    display: flex;
    align-items: center;
}

.decoratorDialog .header-buttons {
    display: flex;
}

.decoratorDialog .topNavButton {
    margin-left: 1rem !important;
    height: 2rem;
    flex-wrap: nowrap;
}

.decoratorDialog .topNavButton:last-child {
    margin-right: 1.5rem;
}

.decoratorDialog .content {
    display: flex;
    min-height: 450px;
    width: 100%;
    padding-right: 1.5rem;
    padding-bottom: 28px;
    box-sizing: border-box;
}

.decoratorDialog .content-leftSide {
    width: 235px;
}

.decoratorDialog .content-rightSide {
    margin: 0px 0px 0px 40px;
    display: block;
    width: 100%;
    height: 99%;
    padding: 0;
    padding-right: 15px;
    overflow: auto;
}

.decoratorDialog .dialogTabs {
    display: flex;
    width: 190px;
    box-shadow:  1px 0px 0px 0px gainsboro;
}

.decoratorDialog .dialogTab {
    display: flex;
    flex-direction: column;
    align-items: flex-start !important;
    max-height: 35px !important;
    min-height: 0 !important;
    font-size: 14px !important;
}

.decoratorDialog .dialogTab_error {
    -webkit-text-decoration: red underline;
            text-decoration: red underline;
}

.decoratorDialog .dialogGrid {
    width: 100%;
    display: grid;
    grid-template-rows: minmax(3.5rem, -webkit-min-content);
    grid-template-rows: minmax(3.5rem, min-content);
    grid-auto-rows: minmax(3.5rem, -webkit-min-content);
    grid-auto-rows: minmax(3.5rem, min-content);
    grid-gap: 0px 1.5rem;
    gap: 0px 1.5rem;
    grid-auto-flow: row;
    align-items: center;
}

.decoratorDialog .dialogGrid_2cols {
    grid-template-columns: auto 1fr; 
}

.decoratorDialog .dialogGrid_2minCols {
    grid-template-columns: -webkit-min-content -webkit-min-content;
    grid-template-columns: min-content min-content; 
}

.decoratorDialog .dialogGrid_flexStartAligned {
    align-items: flex-start;
}

.decoratorDialog .dialogGrid_topIndented {
    margin-top: 12px;
}

.decoratorDialog .dialogGrid_leftIndented {
    padding-left: 15px;
    box-sizing: border-box;
}

.decoratorDialog .dialogGrid_mediumWidth {
    min-width: 500px;
}

.decoratorDialog .dialogGrid_rightAlignedLabels span {
    text-align: right;
}

.decoratorDialog .textButton {
    margin-left: 1rem !important;
}

.decoratorDialog span {
    white-space: nowrap;
}

.decoratorDialog .smallText {
    font-size: 14px;
    color: dimgrey;
}

.decoratorDialog .highlightedOption {
    font-weight: bold;
}

.decoratorDialog .labelWithAsterisk {
    display: flex;
    width: 100%;
}

.decoratorDialog .labelWithAsterisk-asteriskBox {
    position: relative;
    width: 0px;
}

.decoratorDialog .labelWithAsterisk-asteriskBox {
    position: relative;
    width: 0px;
}

.decoratorDialog .dialogGrid_rightAlignedLabels .labelWithAsterisk-labelBox {
    text-align: right;
}

.decoratorDialog .dialogGrid_rightAlignedLabels .labelWithAsterisk-labelBox {
    width: 100%;
}

.decoratorDialog .labelWithAsterisk-asteriskMark {
    color: tomato;
    font-weight: bold;
    margin-top: -5px;
    margin-left: 5px;
    position: absolute;
}

.decoratorDialog .inputWithAdornment {
    display: flex;
    align-items: center;
}

.decoratorDialog .numberInput {
    width: 110px;
}

.decoratorDialog .unicodeInput {
    width: 120px;
}

.decoratorDialog .bulletInput {
    min-width: 50px;
    max-width: 50px;
}

.decoratorDialog .mediumTextField {
    width: 223px;
}

.decoratorDialog .largeTextField {
    width: 400px;
    display: inherit;
}

.decoratorDialog .colorSample {
    width: 1.5rem;
    height: 1.5rem;
    border-radius: 100%;
    margin-left: 0.75rem;
    cursor: pointer;
    box-shadow: 1px 1px 9px 0px grey;
}

.decoratorDialog .colorField {
    display: flex;
    align-items: center;
}

.decoratorDialog .previewSide {
    align-self: flex-start;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-end; 
    padding-left: 35px;
    padding-right: 10px;
}

.decoratorDialog .unlabeledCheckbox {
    margin-left: -11px;
}

.decoratorDialog .labeledCheckbox {
    margin-right: 1.3rem;
}

.decoratorDialog .checkBoxesSet {
    display: flex;
}

.decoratorDialog .flexibleGrid {
    width: 100%;
    display: grid;
    grid-template-rows: minmax(0, -webkit-min-content);
    grid-template-rows: minmax(0, min-content);
    grid-auto-rows: minmax(0, -webkit-min-content);
    grid-auto-rows: minmax(0, min-content);
    grid-gap: 0px 1.5rem;
    gap: 0px 1.5rem;
    grid-auto-flow: row;
    align-items: center;
    align-content: center;
    grid-template-columns: -webkit-min-content -webkit-min-content;
    grid-template-columns: min-content min-content; 
}

.decoratorDialog .flexibleGrid>span {
    height: 3.5rem;
    display: flex;
    align-items: center;
}

.decoratorDialog .optionalField {
    display: inline-flex;
    width: 100%;
    -webkit-transition: all 0.4s ease-out;
    transition: all 0.4s ease-out;
    align-items: center;
}

.decoratorDialog .optionalField_hidden {
    opacity: 0;
    height: 0;
    overflow: hidden;
}

.decoratorDialog .optionalField_shown {
    opacity: 1;
    height: 3.5rem;
}
.App {
  display: flex;
  justify-content: center;
}

.startButtons {
  margin-top: 30px;
  display: flex;
  flex-direction: column;
}
body {
  margin: 0;
  font-family: "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
